<script setup>
import { ref, onMounted } from "vue";
import { useStore } from "vuex";
const store = useStore();
const card_search = ref("");
const db_test = ref([]);
const search_db = ref([]);
onMounted(() => {
  db_test.value = store.getters.get_test_db;
  // console.log(db_test.value);
});
const click_search = () => {
  search_db.value = [];
  for (let i = 0; i < db_test.value.length; i++) {
    if (db_test.value[i].card_id === card_search.value) {
      search_db.value.push(db_test.value[i]);
    }
  }
};
</script>
<template>
  <div class="row m-0">
    <div class="col-12">
      <div class="row my-3">
        <div class="col-4 col-md-2 text-center my-auto">รหัสประชาชน</div>
        <div class="col-8 col-md-4 mt-1">
          <div class="was-validated">
            <input
              type="text"
              class="form-control"
              v-model="card_search"
              placeholder="xxxxxxxxxxxxx"
              required
            />
          </div>
        </div>
        <div class="col-12 col-md-3">
          <div class="d-grid gap-1 col-12 mt-1">
            <button class="btn btn-primary" type="button" @click="click_search">
              <i class="bi bi-search me-1"></i>ค้นหาข้อมูลลงทะเบียน
            </button>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-12">
          <template v-if="search_db != ''">
            <div class="row row-cols-1 row-cols-md-2 g-4">
              <div
                class="col"
                v-for="(val, index) in search_db"
                v-bind:key="index"
              >
                <div class="card">
                  <div class="row g-0">
                    <div class="col-md-4 my-auto">
                      <template v-if="val.image ?? ''">
                        <img
                          :src="val.image"
                          class="img-fluid rounded-start"
                          :alt="index"
                        />
                      </template>
                      <template v-else
                        ><i class="bi bi-person-circle h1"></i
                      ></template>
                    </div>
                    <div class="col-md-8">
                      <div class="card-body">
                        <h5 class="card-title">{{ val.card_id }}</h5>
                        <p class="card-text text-start">
                          {{ val.pname }} {{ val.fname }} {{ val.lname }}
                        </p>
                        <p class="card-text text-start">{{ val.email }}</p>
                        <p class="card-text text-start">
                          {{ val.education_level }}
                        </p>
                        <p class="card-text text-start">
                          {{ val.present_type }}
                        </p>
                        <p class="card-text text-start">{{ val.topic_type }}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
